<template>
	<div class="archive">
		<basemap title="Archive" class="basemap">
			<template #default>
				<ul class="archive-list" v-if="complete">
					<li class="archive-list-item"
						v-for="(article, i) in articles"
						:key="i">
						<router-link :to="{path: '/article', query: { title: article }}">
							{{ article }}
						</router-link>
					</li>
				</ul>
			</template>
		</basemap>
	</div>
</template>

<script>
	
	import get from '@/network/get'
	import Basemap from '@/components/Basemap/Basemap'
	
	export default {
		name: 'Archive',
		data() {
			return {
				articles: null,
				complete: false
			}
		},
		components: {
			Basemap
		},
		created() {
			get('/article/all').then(data => {
				this.articles = data
				this.complete = true
			})
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	.archive
		width 100%
		height 100vh
	
		.archive-list
			margin-top 4rem
		
			.archive-list-item
				color var(--secondary)
				margin 0 2rem
				list-style disc
				line-height 3rem
				
		@media screen and (max-width: 400px)
			.basemap >>> .basemap-content
				width 90%

</style>

